import React from 'react'
import logo from './../Image/Logo.png'
import { Link } from 'react-router-dom'
import { useState } from 'react'

const Navigation = () => {
    const [menuOn, setMenuOn] =useState(false)
    return (
        <div className = 'NavigationBar dFr'>
            <Link to = "/">
                <img src = {logo} alt = 'logo' className = 'logoNav'/>
            </Link>
            <div>
                <div className = 'menu smallScreen' onClick = {() => setMenuOn(!menuOn)}>Menu</div>
                {menuOn && 
                    <div className = 'animationOverflow'>
                        <ul className = 'ulNav smallScreen'>
                            <Link to = "/" className = 'liNav' onClick = {() => setMenuOn(!menuOn)}>
                                Home
                            </Link>
                            <Link to = "/dataBase" className = 'liNav' onClick = {() => setMenuOn(!menuOn)}>
                                Data-Base
                            </Link>
                            <Link to = "/CRUD" className = 'liNav' onClick = {() => setMenuOn(!menuOn)}>
                                CRUD
                            </Link>
                            <Link to = "/about" className = 'liNav' onClick = {() => setMenuOn(!menuOn)}>
                                About
                            </Link>
                        </ul>
                    </div>}

                <ul className = 'ulNav bigScreen'>
                    <Link to = "/" className = 'liNav'>
                        Home
                    </Link>
                    <Link to = "/dataBase" className = 'liNav'>
                        Data-Base
                    </Link>
                    <Link to = "/CRUD" className = 'liNav'>
                        CRUD
                    </Link>
                    <Link to = "/about" className = 'liNav'>
                        About
                    </Link>
                </ul>
            </div>
        </div>
    )
}

export default Navigation
