import React from 'react'
import Aos from "aos";

const ItemsCRUD = ( {fetchedData, onDelete, onUpdate, appear} ) => {

        Aos.init({duration:800})

    return (
        <div className = 'itemsCrud'>
            {fetchedData.map((element) => (
                <div key = {element.phone}
                       className = "item"> 
                    <div className = 'itemImage'>
                        <div className = 'circle'>
                            {element.name[0] + '.' + element.surname[0]}

                        </div>
                    </div>
                    <div className = 'dFc'>
                        <div className = 'itemTitle'>
                                {element.name+' '+element.surname} 
                                <div className = 'remEdit'>
                                    {element.id == '1'? <div className = 'rembtn'>Kept</div> :<div className = 'rembtn' onClick = {() => onDelete(element.phone)}>
                                        Remove
                                    </div>}
                                    <div className = 'rembtn' 
                                        onClick = {() => {onUpdate({
                                            image: element.image,
                                            name: element.name,
                                            surname: element.surname,
                                            sex: element.sex,
                                            country: element.country,
                                            adress:element.adress,
                                            studies: element.studies,
                                            studies_section:element.studies_section,
                                            domain: element.domain,
                                            married: element.married,
                                            work:element.work,
                                            email: element.email,
                                            phone: element.phone
                                        });
                                        appear()}
                                        }>
                                        Upadte
                                    </div>
                                </div>

                        </div>
                        <div className = 'itemDescription'>
                            <div className = 'dFc oneColumn'>
                                <div className = 'oneDescriptoion'>
                                    <div className = 'dataDesc'>Name:</div> 
                                    <div className = 'dataElem'>{element.name}</div>
                                </div>
                                <div className = 'oneDescriptoion'>
                                    <div className = 'dataDesc'>Surname:</div> 
                                    <div className = 'dataElem'>{element.surname}</div>
                                </div>
                                <div className = 'oneDescriptoion'>
                                    <div className = 'dataDesc'>Sex:</div> 
                                    <div className = 'dataElem'>{element.sex === 'M'? 'Male': 'Female'}</div>
                                </div>
                                <div className = 'oneDescriptoion'>
                                    <div className = 'dataDesc'>Country:</div> 
                                    <div className = 'dataElem'>{element.country}</div>
                                </div>
                                <div className = 'oneDescriptoion'>
                                    <div className = 'dataDesc'>Adress:</div> 
                                    <div className = 'dataElem'>{element.adress}</div>
                                </div>
                                <div className = 'oneDescriptoion'>
                                    <div className = 'dataDesc'>Studies:</div> 
                                    <div className = 'dataElem'>{element.studies}</div>
                                </div>
                            </div>
                            <div className = 'dFc'>
                                <div className = 'oneDescriptoion'>
                                    <div className = 'dataDesc'>Domain:</div> 
                                    <div className = 'dataElem'>{element.studies_section}</div>
                                </div>
                               
                                <div className = 'oneDescriptoion'>
                                    <div className = 'dataDesc'>Married:</div> 
                                    <div className = 'dataElem'>{element.married === 'Y'? "Yes": "No"}</div>
                                </div>
                                <div className = 'oneDescriptoion'>
                                    <div className = 'dataDesc'>Work:</div> 
                                    <div className = 'dataElem'>{element.work}</div>
                                </div>
                                <div className = 'oneDescriptoion'>
                                    <div className = 'dataDesc'>Email:</div> 
                                    <div className = 'dataElem'>{element.email}</div>
                                </div>
                                <div className = 'oneDescriptoion'>
                                    <div className = 'dataDesc'>Phone:</div> 
                                    <div className = 'dataElem'>{element.phone}</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>           
            ))}
        </div>
    )
}

export default ItemsCRUD
