import React from 'react'
import axios from 'axios'
import Items from './Items'
import { useState, useEffect} from 'react'
import{Link} from 'react-router-dom'

const DataBase = () => {
    const [fetchedData, setFetchedData] = useState([])
    const fetchData = () => {
        
         axios.get('/api/get',{mode: 'cors'}).then((res) => {
             setFetchedData(res.data.reverse())
         })
    }
    useEffect(() => {
        fetchData()
    },[])
    
    return (
       <div className = 'DATABASE'>
           <Items fetchedData = {fetchedData}/>
            <div className = 'staticWord backAnim'>
                Data-Base
                <div className = 'staticWord2'>
                    Constructed with SQL
                </div>
                <Link to = "/CRUD" className = 'buttonCrud backButAnim'>
                    EDIT
                </Link>
            </div>
        </div>
    )
}

export default DataBase
