import React from 'react'
import axios from 'axios'
import { useState, useEffect} from 'react'
import{Link} from 'react-router-dom'
import Form from './Form'
import ItemsCRUD from './ItemsCrud'

const CRUD = () => {

    const [submitData, SetSubmitData] = useState({})
    const [fetchedData, setFetchedData] = useState([])
    const [popEdit, setPopEdit] = useState(false)
    const [dataToUpdate, setDataToUpdate] = useState(undefined)
    const [smallScreenFormPop, setSmallScreenFormPop] = useState(false)

    const fetchData = () => {
        axios.get('http://localhost:5000/api/get',{mode: 'cors'}).then((res) => {
            setFetchedData(res.data.reverse())
        })
   }

    const data = (somData) => {
        SetSubmitData(somData)
        }

    const onSubmit = (e) => {
        e.preventDefault();
        let dataToPost = submitData
        axios.post('/api/insert', dataToPost)
        setFetchedData([dataToPost,...fetchedData])
        dataToPost = []
        e.target.reset()
    }
    const deleteItem = (element) => {
       axios.delete(`/api/delete/${element}`)
        setFetchedData(fetchedData.filter(row => row.phone !== element))
    }
    const updateItem =(obj) => {
        setDataToUpdate(obj)
        setPopEdit(true)
            // axios.put('http://localhost:5000/api/insert', dataToPost)
    }   
    useEffect(() => {
        fetchData()
    },[])
  

    return (
        <div className = 'DATABASE'>
            {!popEdit && <ItemsCRUD 
                fetchedData = {fetchedData}
                onDelete = {deleteItem}
                onUpdate = {updateItem}
                appear = {() => setSmallScreenFormPop(true)}/>
               }

         <div className = {!popEdit ? 'staticWord DatAnim' : undefined}>
         {!popEdit && 
            <div>
                Data-Base
                <div className = 'staticWord2'>
                    Constructed with SQL
                </div>
             </div>}
             {!popEdit && <div className = 'addButton smallScreen' onClick = {() => setSmallScreenFormPop(!smallScreenFormPop)}>
                 Add
             </div>}
             <Link to = "/CRUD" className = 'buttonCrud ButAnim'>
                 EDIT
             </Link>
            
             {smallScreenFormPop && <div className = 'smallScreen'>
                <Form 
                onSubmit = {onSubmit}
                data = {data}
                fetchedData = {fetchedData}
                dataToUpdate = {dataToUpdate}
                popEdit = {popEdit}
                fetchAll = {() => fetchData()}
                />
             </div>}
             <div className = 'bigScreen'>
                <Form
                    onSubmit = {onSubmit}
                    data = {data}
                    fetchedData = {fetchedData}
                    dataToUpdate = {dataToUpdate}
                    popEdit = {popEdit}
                    fetchAll = {() => fetchData()}
                />
             </div>
            
         </div>
     </div>
    )
}
export default CRUD
