import axios from 'axios'
import React from 'react'
import { useState } from 'react'


const Form = ( {onSubmit, data, fetchedData,fetchAll, dataToUpdate, popEdit} ) => {

const [modName, setModName] = useState(false)
const [modSurName, setModSurName] = useState(false)
const [modCountry, setModCountry] = useState(false)
const [modAdress, setModAdress] = useState(false)
const [modStudies, setModStudies] = useState(false)
const [modDomain, setModDomain] = useState(false)
const [modWork, setModWork] = useState(false)

const [person_image, setPerson_image] = useState(popEdit? dataToUpdate.name: '' )
const [name, setName] = useState('')
const [surname, setSurame] = useState('')
const [sex, setSex] = useState('')
const [country, setCountry] = useState('')
const [adress, setAdress] = useState('')
const [studies, setStudies] = useState('')
const [studies_section, setStudies_section] = useState('')
const [married, setMarried] = useState('')
const [work, setWork] = useState('')
const [email, setEmail] = useState('')
const [phone, setPhone] = useState('')
const [popUp, setPopUp] = useState(false)

const nameSurCorrect = (namSur) => {
    if(namSur === '' || namSur.length<3) {
        return true
    } else {
        return false
    }
}
const emailUnique = (givenEmailEntered) => {
    let givenEmail = givenEmailEntered.replace(/\s+/g, '')
    const arrEmail = []
    let final = true
    fetchedData.forEach(element => {
        arrEmail.push(element.email.replace(/\s+/g, ''))
    });
    arrEmail.forEach(element1 => {
        if(!givenEmail.includes('@') || !givenEmail.includes('.com') || element1 === givenEmail || givenEmail.length <8 ) {
            final = true
        } else {
            final = false
        }    
    });
    return final
}
const phoneUnique = (givenPhoneEntered) => {
    let givenPhone = givenPhoneEntered.replace(/\s+/g, '')
    const arrPhone = []
    let final = true
    fetchedData.forEach(element => {
        arrPhone.push(element.phone.replace(/\s+/g, ''))
    });
    arrPhone.forEach(element1 => {
        if(givenPhone.match(/^[0-9]+$/) === null || element1 === givenPhone || givenPhone.length <5 ) {
            final = true
        } else {
            final = false
        }
    });
    return final
}

const everyThingGood = () => {
    let final
    if (phoneUnique(phone) || emailUnique(email) || nameSurCorrect(name) || nameSurCorrect(surname)) {
        final = false
    } else {
        final = true
    }
    return final
}

const onEdit = (e) => {
    e.preventDefault()
    e.target.reset()
    fetchAll()
    let atributes = [person_image, name, surname, sex, country, adress, studies, studies_section, married, work, email, phone]
    let takenData = [dataToUpdate.person_image,dataToUpdate.name, dataToUpdate.surname, dataToUpdate.sex, dataToUpdate.country, dataToUpdate.adress, dataToUpdate.studies, dataToUpdate.studies_section, dataToUpdate.married, dataToUpdate.work, dataToUpdate.email, dataToUpdate.phone]
    for (let i = 0; i < atributes.length; i++) {
       if (atributes[i] !== '' && atributes[i] !== takenData[i]) {
           takenData[i] = atributes[i]
       }
      }

    let dataToSend = {
        person_image: takenData[0],
        name :  takenData[1],
        surname :  takenData[2],
        sex :  takenData[3],
        country :  takenData[4],
        adress :  takenData[5],
        studies :  takenData[6],
        studies_section :  takenData[7],
        married :  takenData[8],
        work :  takenData[9],
        email : takenData[10],
        phone : takenData[11],
    }
      axios.put('/api/update', dataToSend)

      setPerson_image('');
      setName('');
      setSurame('');
      setSex('');
      setCountry('');
      setAdress('');
      setStudies('');
      setStudies_section('');
      setMarried('');
      setWork('');
      setEmail('');
      setPhone('');
      setPopUp(false);
      setModName(false)
      setModSurName(false)
      window.location.reload();
}
    return (
        <div className = {popEdit? 'bigPop':'form'}>
            {popUp && 
                <div className = 'popUpThank'>
                    <div className='dFc'>
                        Thank you
                    <button 
                        className = 'thxBtn' 
                        onClick = {() => {
                            setPerson_image('');
                            setName('');
                            setSurame('');
                            setSex('');
                            setCountry('');
                            setAdress('');
                            setStudies('');
                            setStudies_section('');
                            setMarried('');
                            setWork('');
                            setEmail('');
                            setPhone('');
                            setPopUp(false);
                           }}>
                        RELOAD
                    </button>
                    </div>
                </div>}
        <form onSubmit = {!popUp? onEdit: onSubmit}>

            <div className = 'modify'>
                <label>First Name</label> 
                {popEdit && <div className = 'dFr'>
                    <div className = 'rembtn' onClick = {() => {setModName(true);
                                                                setName(dataToUpdate.name)}}> Modify </div>
                </div>}
            </div>
       
            {!popEdit? nameSurCorrect(name) ? 
                <div className = 'error'>You are required to complete this field!</div>: 
                <div className = 'confirmation'>Seems to be good looking</div>:''}
            {popEdit? modName? nameSurCorrect(name) ? 
                <div className = 'error'>You are required to complete this field!</div>: 
                <div className = 'confirmation'>Seems to be good looking</div>:'':''}
            <input type = "text" 
                placeholder = 'What is your First Name?'
                onChange = {(e) => setName(e.target.value)}
                defaultValue = {popEdit? dataToUpdate.name: ''}
                readOnly={popEdit && !modName && "readonly"}
                className = {popEdit? modName? '':'afterInput': ''}
                />
           
            <div className = 'modify'>
                <label>Second Name</label>
                {popEdit && <div className = 'dFr'>
                    <div className = 'rembtn' onClick = {() => {setModSurName(true);
                                                                setSurame(dataToUpdate.surname)}}> Modify </div>
                </div>}
            </div>
            {!popEdit? nameSurCorrect(surname) ? 
                <div className = 'error'>You are required to complete this field!</div>: 
                <div className = 'confirmation'>Seems to be good looking</div>:''}
            {popEdit? modSurName? nameSurCorrect(surname) ? 
                <div className = 'error'>You are required to complete this field!</div>: 
                <div className = 'confirmation'>Seems to be good looking</div>:'':''}
            <input 
                type = 'text' 
                placeholder = 'What is your Second Name?'
                defaultValue = {popEdit? dataToUpdate.surname: ''}
                onChange = {(e) => {setSurame(e.target.value)}}
                readOnly={popEdit && !modSurName && "readonly"}
                className = {popEdit? modSurName? '':'afterInput': ''}/>

            <div className = 'modify'>
            <label>Sex</label>
            </div>
            <div className = 'dFc'>
                <div className = 'dFrc'>
                    <input className = 'radio'
                        defaultChecked = {popEdit? dataToUpdate.sex === 'F'? true: false: undefined}
                        type = 'radio' 
                        id = 'Female'
                        name="sex"
                        value = "F"
                        onChange = {(e) => setSex(e.target.value)}/>
                    <label>Female</label>
                </div>
                <div className = 'dFrc'>
                    <input className = 'radio'
                        defaultChecked = {popEdit? dataToUpdate.sex === 'M'? true: false: undefined}
                        type = 'radio' 
                        id = 'Male'
                        name="sex"
                        value = "M"
                        onChange = {(e) => setSex(e.target.value)}/>
                    <label>Male</label>
                </div>
            </div>
           
            <div className = 'modify'>
                <label>Country</label>
                {popEdit && <div className = 'dFr'>
                    <div className = 'rembtn' onClick = {() => setModCountry(true)}> Modify </div>
                </div>}
            </div>
            <input 
                type = 'text' 
                defaultValue = {popEdit? dataToUpdate.country: ''}
                placeholder = 'You are citizen of the state ...'
                onChange = {(e) => setCountry(e.target.value)}
                readOnly={popEdit && !modCountry && "readonly"}
                className = {popEdit? modCountry? '':'afterInput': ''}/>
                
            
            <div className = 'modify'>
                <label>Adress</label>
                {popEdit && <div className = 'dFr'>
                    <div className = 'rembtn' onClick = {() => setModAdress(true)}> Modify </div>
                </div>}
            </div>
            <input 
                type = 'text' 
                defaultValue = {popEdit? dataToUpdate.adress: ''}
                placeholder = 'Where do you live?'
                onChange = {(e) => setAdress(e.target.value)}
                readOnly={popEdit && !modAdress && "readonly"}
                className = {popEdit? modAdress? '':'afterInput': ''}/>

            <div className = 'modify'>
                <label>Studies</label>
                {popEdit && <div className = 'dFr'>
                    <div className = 'rembtn' onClick = {() => setModStudies(true)}> Modify </div>
                </div>}
            </div>
            <input 
                type = 'text' 
                defaultValue = {popEdit? dataToUpdate.studies: ''}
                placeholder = 'What is your highest level of education?'
                onChange = {(e) => setStudies(e.target.value)}
                readOnly={popEdit && !modStudies && "readonly"}
                className = {popEdit? modStudies? '':'afterInput': ''}/>
            
            <div className = 'modify'>
                <label>Domain</label>
                {popEdit && <div className = 'dFr'>
                    <div className = 'rembtn' onClick = {() => setModDomain(true)}> Modify </div>
                </div>}
            </div>
            <input 
                type = 'text' 
                defaultValue = {popEdit? dataToUpdate.studies_section: ''}
                placeholder = 'What field did you studied'
                onChange = {(e) => setStudies_section(e.target.value)}
                readOnly={popEdit && !modDomain && "readonly"}
                className = {popEdit? modDomain? '':'afterInput': ''}/>   

            <div className = 'modify'>
                <label>Are you Married</label>
            </div>
           
            <div className = 'dFc'>
                <div className = 'dFrc'>
                    <input className = 'radio'
                        type = 'radio' 
                        defaultChecked = {popEdit? dataToUpdate.married === 'Y'? true: false: undefined}
                        id = 'Yes'
                        name="married"
                        value = "Y"
                        onChange = {(e) => setMarried(e.target.value)}/>
                    <label>Yes</label>
                </div>
                <div className = 'dFrc'>
                    <input className = 'radio'
                        type = 'radio'
                        defaultChecked = {popEdit? dataToUpdate.married === 'N'? true: false: undefined} 
                        id = 'No'
                        name="married"
                        value = "N"
                        onChange = {(e) => setMarried(e.target.value)}/>
                    <label>No</label>
                </div>
            </div>
            
             <div className = 'modify'>
             <label>Work</label>
                {popEdit && <div className = 'dFr'>
                    <div className = 'rembtn' onClick = {() => setModWork(true)}> Modify </div>
                </div>}
            </div>
            <input 
                type = 'text' 
                defaultValue = {popEdit? dataToUpdate.work: ''}
                placeholder = 'What do you currently work?'
                onChange = {(e) => setWork(e.target.value)}
                readOnly={popEdit && !modWork && "readonly"}
                className = {popEdit? modWork? '':'afterInput': ''}/>

            
            <div className = 'modify'>
            <label>Email</label>
            </div>
            {!popEdit? emailUnique(email) ? 
                <div className = 'error'>Your Email must be Unique and to contain @ and .com</div>: 
                <div className = 'confirmation'> Email { email } seems to be good looking </div>:''}
            <input 
                type = 'text' 
                defaultValue = {popEdit? dataToUpdate.email: ''}
                placeholder = 'Write your email'
                onChange = {(e) => setEmail(e.target.value)}
                readOnly={popEdit && "readonly"}
                className = {popEdit?'afterInput': ''}/>

            <div className = 'modify'>
                <label>Phone</label>
            </div>
            {!popEdit? phoneUnique(phone) ? 
                <div className = 'error'>Your phone must be 5 characters mininum, all digit and unique</div>: 
                <div className = 'confirmation'> Number { phone } seems to be good looking </div>:''}
            <input 
                type = 'text' 
                placeholder = 'Write your phone number'
                defaultValue = {popEdit? dataToUpdate.phone: ''}
                onChange = {(e) => setPhone(e.target.value)}
                readOnly={popEdit && "readonly"}
                className = {popEdit?'afterInput': ''}/>

            {!popEdit && everyThingGood() && 
                 <button 
                className = 'submit'
                onClick = {() => {data({
                    person_image: person_image,
                    name:name,
                    surname:surname,
                    sex:sex,
                    country : country,
                    adress : adress,
                    studies : studies,
                    studies_section : studies_section,
                    married : married,
                    work : work,
                    email : email,
                    phone : phone,
                });
                setPopUp(true);
                window.scrollTo(0,0)
            }}
                >Add
                </button>}
                {popEdit && <button 
                    className = 'submit' >
                        Edit
                </button>}
        </form>
        </div>
    )
}

export default Form
